.name{
    font-size: 1.7em;
    color: #ff4d4d;
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
margin-top: 1em;
}
body{
    background-color: #273c75;
}
.confirm{
    background-color: #e67e22 ;
    border: 1 px solid #34495e ;
  
  }
  .card-title{
font-size: 1.5em;
font-weight: bold; 
color: white;
font-family: "Lucida Console", Courier, monospace;
}
  .card-body{
      font-size: 3em;
      font-weight: bold;
  }
  
  .recovered{
    background-color: #2ecc71 ;
    border: 1 px solid #34495e;
  
  }
  .card{
      border-radius: 30px;
      box-shadow: 10px 10px 8px #04332f;
      
  }
  .card-deck{
      margin-top: 4em;
  }
  
  .deaths{
    background-color: #e74c3c ;
    border: 1 px solid #34495e;
  
  }
  
.lastupdate{
  font-size: 0.7em;
}
.number{
  font-size: 1em;
}
@media only screen and (max-width: 1000px) {
  .card {
    margin: 2% 0 ;
  }
}